<template>
  <div>
    <el-dialog
      :title="update == true ? '角色添加' : '菜单修改'"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form">



    <el-form-item label="上级分类" :label-width="formLabelWidth">
              <el-select v-model="form.pid">
                <el-option label="顶级菜单" :value="0"></el-option>
                <el-option :label="item.catename" :value="item.id"
                v-for="(item,index) in cateList" :key="index"></el-option>
              </el-select>
            </el-form-item>





        <el-form-item
          label="角色名称"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.catename" autocomplete="off"></el-input>
        </el-form-item>


        <!-- 用户头像上传 -->
      <el-form-item label="图片"  :label-width="formLabelWidth">
        <div class="box">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="change">
            <img v-if="imgurl" :src="imgurl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </div>
          
      </el-form-item>


        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
            active-color="#13ce66"
            inactive-color="#ff4949"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="addcateList" v-if="update"
          >添加</el-button
        >
        <el-button type="primary" @click="updatecate" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { indexRouters } from "../../../router/index";
import { alertSuccess, alertWarn } from "../../../utils/alert";
import { cateadd, cateedit, cateinfo } from "../../../utils/request";
export default {
  props: ["update"],
  components: {},
  data() {
    return {
        defaultProps:{
            children: 'children',
            label: 'title'
        },
      imgurl:"",
      dialogFormVisible: false,
      form: {
        catename:"",
        img:"",
        status:1
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      cateList:"cateList"
    }),
  },
  methods: {
    ...mapActions({
      getcateList:"getcateList"
    }),
    // 上传图片失败执行的回调
    change(e){
      let file = e.raw
      this.imgurl = URL.createObjectURL(file)
      this.form.img=file
    },
    cancle(){
        this.dialogFormVisible=false;
        this.clearForm()
    },
    clearForm() {
      this.form = {
        catename:"",
        img:"",
        status:1
      };
    },
    addcateList() {
      this.dialogFormVisible = false;
      cateadd(this.form).then((res) => {
        this.getcateList()
        this.clearForm();
      });
    },
    getcateDetail(id) {
      cateinfo(id).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imgurl=this.$imgurl+this.form.img
      });
    },
    updatecate() {
      cateedit(this.form).then((res) => {
        if (res.data.code == 200) {
          alertSuccess(res.data.msg);
          this.dialogFormVisible = false;
          this.clearForm();
          this.getcateList();
        } else {
          alertWarn(res.data.msg);
        }
      });
    },
  },
    mounted() {
      this.getcateList()
  },
};
</script>
<style scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .box{
    width: 178px;
    height: 178px;
    border: 1px dashed #ccc;
  }
</style>


